* {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 1.5rem;
}

body {
  color: #fff;
  background: #191936;
  padding: 6rem;
}

input {
  color:#191936;
  font-size: 1.3rem;
  width: 100%;
}

.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 5px;
  overflow: hidden;
  margin-bottom: 1rem;
  margin-right: 1rem;
}

.card p {
  text-align: center;
  height: 2rem;
  width: 10rem;
}

.card img {
  height: 10rem;
  width: 10rem;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}